import React, {useEffect} from 'react'
import {TypographyTitle} from "../../../../styles/common-components/Typography-styled";
import InputBox from "../../../common/InputBox/InputBox";
import {ButtonWrapper, QuizAgeStyled} from "./QuizAge-styled";
import {Button} from "../../../common/Buttons/Button";
import { useToasts } from 'react-toast-notifications'

export default ({setFieldValue, handlePageNext, error, currentValue, currentKey, handleChange, handleBlur}) => {

    const handleConfirm = value => {
        setFieldValue([currentKey], value)
        handlePageNext()
    }

    const handleChangeValue = e => {
        handleChange(e)
    }

    useEffect(() => {
        if (!!error)
            addToast(error, {
                appearance: 'error',
                autoDismiss: true,
            })
    }, [error])

    const { addToast } = useToasts()

    return (
            <QuizAgeStyled>
                <TypographyTitle align="center" style={{marginTop:"20px",marginBottom:"60px"}}>
                    How old are you?
                </TypographyTitle>
                <InputBox
                    name={currentKey}
                    type="number"
                    min={18}
                    max={90}
                    value={currentValue}
                    onChange={handleChangeValue}
                />
                <ButtonWrapper>
                    <Button
                        type="button"
                        isActive={!error}
                        onClick={() => handlePageNext()}
                    >
                        Next
                    </Button>
                </ButtonWrapper>
            </QuizAgeStyled>
    )

}

